import { Injectable } from '@angular/core'
import {
  Camera,
  CameraDirection,
  CameraResultType,
  CameraSource,
  Photo,
} from '@capacitor/camera'
import { Filesystem, Directory } from '@capacitor/filesystem'
import { FileUploadService } from 'src/api-cyclist'
import { firstValueFrom } from 'rxjs'
import { LoadingController } from '@ionic/angular'
import Compressor from 'compressorjs'
import { ToastService, ToastType } from './toast.service'
import { TranslateService } from '@ngx-translate/core'
import { ErrorHandlerService } from './error-handler.service'

@Injectable({
  providedIn: 'root',
})
export class MediaService {
  constructor(
    private uploadService: FileUploadService,
    private loadingController: LoadingController,
    private toastService: ToastService,
    private translateService: TranslateService,
    private errorHandler: ErrorHandlerService
  ) {}

  // Photo specific methods
  private async capturePhoto() {
    return await Camera.getPhoto({
      resultType: CameraResultType.Uri,
      source: CameraSource.Camera,
      direction: CameraDirection.Rear,
      saveToGallery: true,
      quality: 100,
    })
  }

  async captureAndProcessPhoto(): Promise<{ photo: Photo; blob: Blob }> {
    const capturedPhoto = await this.capturePhoto()
    const response = await fetch(capturedPhoto.webPath!)
    const originalBlob = await response.blob()
    const compressedBlob = await this.compressFile(originalBlob)
    if (!compressedBlob) {
      throw new Error('File compression failed')
    }
    return { photo: capturedPhoto, blob: compressedBlob }
  }

  // File specific methods
  async selectAndProcessFile(): Promise<{ uri: string; blob: Blob }> {
    return new Promise((resolve, reject) => {
      const fileInput = document.createElement('input')
      fileInput.type = 'file'
      fileInput.accept = 'image/*,application/pdf'

      fileInput.onchange = async (e: any) => {
        const file = e.target.files[0]
        if (!file) {
          this.toastService.createWithJustMessage(
            this.translateService.instant('media.error.no_file_selected'),
            ToastType.Error
          )
          reject(new Error('No file selected'))
          return
        }

        try {
          const compressedBlob = await this.compressFile(file)
          if (!compressedBlob) {
            reject(new Error('File size error'))
            return
          }
          const reader = new FileReader()
          reader.onload = async (e) => {
            const base64Data = (e.target?.result as string).split(',')[1]

            const fileName = `file_${new Date().getTime()}.${file.name
              .split('.')
              .pop()}`
            await Filesystem.writeFile({
              path: fileName,
              data: base64Data,
              directory: Directory.Data,
            })

            const { uri } = await Filesystem.getUri({
              path: fileName,
              directory: Directory.Data,
            })

            resolve({
              uri,
              blob: compressedBlob,
            })
          }
          reader.onerror = () =>
            reject(new Error('Erreur de lecture du fichier'))
          reader.readAsDataURL(compressedBlob)
        } catch (error) {
          this.errorHandler.handleError(error)
          reject(error)
        }
      }

      fileInput.click()
    })
  }

  // Shared methods
  async uploadMedia(blob: Blob): Promise<string> {
    const loading = await this.loadingController.create({
      message: this.translateService.instant('media.uploading'),
    })
    await loading.present()

    const base64String = await this.blobToBase64(blob)
    try {
      const uploadResult = await firstValueFrom(
        this.uploadService.filesUpload({ body: { content: base64String } })
      )
      await loading.dismiss()
      return uploadResult.url ?? ''
    } catch (error) {
      await loading.dismiss()
      this.errorHandler.handleError(error)
      throw error
    }
  }

  private blobToBase64(blob: Blob): Promise<string> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader()
      reader.onloadend = () => {
        const base64String = reader.result as string
        resolve(base64String.split(',')[1])
      }
      reader.onerror = reject
      reader.readAsDataURL(blob)
    })
  }

  private async compressFile(file: Blob): Promise<Blob | undefined> {
    // Skip compression for PDFs
    if (file.type === 'application/pdf') {
      if (file.size > 5900000) {
        this.toastService.createWithJustMessage(
          this.translateService.instant('media.error.file_too_large'),
          ToastType.Error
        )
        return undefined
      }
      return file
    }

    return new Promise((resolve, reject) => {
      new Compressor(file, {
        quality: 0.8,
        maxWidth: 2000,
        maxHeight: 2000,
        success: (compressedFile) => {
          if (compressedFile.size > 6 * 1024 * 1024) {
            this.toastService.createWithJustMessage(
              this.translateService.instant('media.error.file_too_large'),
              ToastType.Error
            )
            resolve(undefined)
            return
          }
          resolve(compressedFile)
        },
        error: (err) => {
          this.errorHandler.handleError(err)
          reject(err)
        },
      })
    })
  }
}

export interface UserPhoto {
  filepath: string
  webviewPath?: string
}
